$background-color: black;
$font-color: #c0c0c0;
$border-color: #909090;
$active-link-background-color: #303030;

@mixin scrollbar() {
  $scrollbar-thumb: $background-color;
  $scrollbar-thumb-border: $font-color;
  $scrollbar-bg: $border-color;

  &::-webkit-scrollbar {
    width: 11px;
    height: 11px;
  }
  & {
    scrollbar-width: thin;
    scrollbar-color: $scrollbar-thumb $scrollbar-bg;
  }
  &::-webkit-scrollbar-track {
    background: $scrollbar-bg;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $scrollbar-thumb;
    border-radius: 4px;
    border: 1px solid $scrollbar-thumb-border;
  }
}
