@import 'styles/theme';

$header-height: 40px;
$footer-height: 0px;
$sidenav-width: 200px;

.app {
  display: grid;
  grid-template-columns: $sidenav-width auto;
  grid-template-rows: $header-height auto $footer-height;
  grid-template-areas:
    'header header'
    'sidenav main'
    'footer footer';

  &-header {
    grid-area: header;
    box-sizing: border-box;
  }

  &-sidenav {
    grid-area: sidenav;
    box-sizing: border-box;
  }

  &-main {
    grid-area: main;
    box-sizing: border-box;
    height: calc(100vh - #{$header-height} - #{$footer-height});
    overflow: auto;
  }

  &-footer {
    grid-area: footer;
    box-sizing: border-box;
  }
}

.app-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid $border-color;
  a {
    text-decoration: none;
    font-weight: bold;
  }
}

.app-sidenav {
  border-right: 1px solid $border-color;
}

.app-main {
  padding: 10px;
}

.app-footer {
  .copyleft {
    position: fixed;
    right: 20px;
    bottom: 15px;
    opacity: 0.7;
  }
}

.copyleft {
  transform: rotate(180deg);
  &::before {
    content: 'Â©';
  }
}
