.notes-list {
  &_title {
    display: inline-block;
    margin: 0.8em 0.8em 0.4em;
    font-weight: bold;
  }

  &_search {
    margin: 0.4em 0.8em;
    box-sizing: border-box;
    width: calc(100% - 1.6em);
  }

  &_list {
    margin: 0.4em 0;
    padding-left: 2em;
  }

  li a {
    text-decoration: none;
  }
}
